@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://use.typekit.net/twf2zjd.css%22);
html,
body,
#root {
	margin: 0;
	padding: 0;
	overflow-x: hidden;
}

::-webkit-scrollbar {
	display: none;
}
* {
}
.mainDiv {
	height: 700vh;
	width: 100vw;
	background: linear-gradient(
		178.33deg,
		#8415aa -0.79%,
		#3d0678 6.05%,
		#100259 12.42%,
		#150864 18.69%,
		#1a1d85 23.78%,
		#2034a9 29.99%,
		#1662d8 36.6%,
		#1f91e4 43.09%,
		#26b1ed 47.72%,
		#71c8ed 52.2%,
		#94d9f6 56.19%,
		#99e0ff 63.55%,
		#6bc9f1 69.44%,
		#77b6e7 73.9%,
		#8895e7 79.37%,
		#9d77d5 84.48%,
		#a938c6 89.96%,
		#8415aa 95.04%,
		#3d0678 98.43%
	);
	background-blend-mode: multiply;
	z-index: 100000;
}

.page1bg {
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
}
.Page1Content {
	width: 50vw;
	height: 60vh;
	background: linear-gradient(184.26deg, #610e91 3.47%, #120352 96.53%);
	box-shadow: 3px 1px 38px -12px #000000;
	border-radius: 28px;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
}
.left,
.right {
	width: 50%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
.equinoxlogo {
	width: 80%;
	/* height: 245px; */
	-webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
	        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.dates {
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	font-size: 2vw;
	line-height: 19px;
	text-align: right;
	margin-top: 0;
	color: #ffffff;
}
.timer {
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	font-size: 4vw;
	line-height: 42px;
	text-align: right;
	color: #ffffff;
	margin-top: 0;
}

#devfolio-apply-now {
	align-items: center;
	outline: none;
	background-color: #3770ff;
	border-radius: 3px;
	border: none;
	color: #ffffff;
	cursor: pointer;
	display: flex;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial,
		sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
	font-size: 18px;
	font-weight: 600;
	padding: 12px 20px;
	text-align: center;
	text-transform: lowercase;
	cursor: pointer;
}

#devfolio-apply-now:not([disabled]) {
	cursor: pointer;
}

#devfolio-apply-now:hover {
	background-color: #2954bf !important;
}

@media screen and (max-width: 720px) {
	.Page1Content {
		width: 80vw;
		height: 65vh;
		flex-direction: column;
	}
	.equinoxlogo {
		width: 70%;
		top: 2%;
	}
	.left,
	.right {
		width: 100%;
	}
	.left {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.dates {
		font-size: 6vw;
	}
	.timer {
		font-size: 9vw;
		margin-bottom: 2%;
	}
}

